@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*===== GOOGLE FONTS =====*/
/*===== VARIABLES CSS =====*/
:root {
  --header-height: 3rem;
  --font-semi: 600;
}

/*===== Colores =====*/
:root {
  --first-color: #4070f4;
  --second-color: #0e2431;
  --third-color: #6d40f5;
  --forth-color: #40cdf5;
}

/*===== Fuente y tipografia =====*/
:root {
  --body-font: "Poppins", sans-serif;
  --big-font-size: 2rem;
  --h2-font-size: 2rem;
  --normal-font-size: 0.938rem;
}

@media screen and (min-width: 768px) {
  :root {
    --big-font-size: 3.5rem;
    --h2-font-size: 3rem;
    --normal-font-size: 1rem;
  }
}

/*===== Margenes =====*/
:root {
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
}

/*===== z index =====*/
:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}

/*===== BASE =====*/
*,
::before,
::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 3rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  font-family: "Poppins", sans-serif;
  font-family: var(--body-font);
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  color: #0e2431;
  color: var(--second-color);
  height: calc(100vh - 3rem);
  height: calc(100vh - var(--header-height));
  background: url(/static/media/Vector.767a7ff6.png) 100% 100%/20% no-repeat, url("/static/media/Vector(1).68d0b99d.png") 100% 0%/60% no-repeat, url("/static/media/Vector(2).9a25e56a.png") -10% 100%/60% no-repeat;
}

h1,
h2,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

/*===== CLASS CSS ===== */
.section-title {
  position: relative;
  font-size: 2rem;
  font-size: var(--h2-font-size);
  color: #4070f4;
  color: var(--first-color);
  margin-top: 1rem;
  margin-top: var(--mb-2);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
  text-align: center;
}

.section-title::after {
  position: absolute;
  content: "";
  width: 5rem;
  height: 0.18rem;
  left: 0;
  right: 0;
  margin: auto;
  top: 2.5rem;
  background-color: #4070f4;
  background-color: var(--first-color);
}

.section {
  padding-top: 3rem;
  padding-bottom: 2rem;
  min-height: calc(100vh - 3rem);
  min-height: calc(100vh - var(--header-height));
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
}

/*===== LAYOUT =====*/
.bd-grid {
  max-width: 1024px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 100%;
      grid-template-columns: 100%;
  grid-column-gap: 2rem;
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-left: var(--mb-2);
  margin-right: 1rem;
  margin-right: var(--mb-2);
}

.l-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  z-index: var(--z-fixed);
}

/*===== NAV =====*/
.nav {
  height: 3rem;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  font-weight: var(--font-semi);
}

@media screen and (max-width: 767px) {
  .nav__menu {
    position: fixed;
    top: 3rem;
    top: var(--header-height);
    right: -100%;
    width: 80%;
    height: 100%;
    padding: 2rem;
    background-color: #0e2431;
    background-color: var(--second-color);
    transition: 0.5s;
  }
}

.nav__item {
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.nav__link {
  position: relative;
  color: #fff;
}

.nav__link:hover {
  position: relative;
}

.nav__link:hover::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: #4070f4;
  background-color: var(--first-color);
}

.nav__logo {
  font-size: 1.3rem;
  font-weight: 500;
  background-color: #a2a5e1;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
  color: #fff;
  padding: 0.1rem 0.5rem;
  border-radius: 1.3rem;
}

.nav__toggle {
  color: #0e2431;
  color: var(--second-color);
  font-size: 1.5rem;
  cursor: pointer;
}

/*Active menu*/
.active::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: #4070f4;
  background-color: var(--first-color);
}

/*=== Show menu ===*/
.show {
  right: 0;
}

/*===== HOME =====*/
.home {
  height: calc(100vh - 3rem);
  height: calc(100vh - var(--header-height));
  grid-row-gap: 1rem;
  row-gap: 1rem;
}

.home__data {
  -ms-grid-row-align: center;
  align-self: center;
}

.home__title {
  margin-bottom: 2.5rem;
  margin-bottom: var(--mb-5);
  text-align: left;
}

.home__title-top {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #36364b;
}

.home__title-mid {
  font-size: 2.5rem;
  letter-spacing: 5px;
  font-weight: 500;
  text-align: left;
  font-family: "Montserrat", sans-serif;
  color: #0e2431;
  color: var(--second-color);
}

.home__title-bot {
  font-size: 1.3rem;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  letter-spacing: 2px;
  color: #444449;
}

.home .home-buttons {
  display: flex;
  justify-content: space-around;
  margin-top: 3rem;
  margin-top: var(--mb-6);
}

/*BUTTONS*/
.button {
  display: inline-flex;
  width: 8.2rem;
  height: 3rem;
  align-items: center;
  justify-content: center;
  font-family: roboto;
  letter-spacing: 1px;
  color: #0e2431;
  color: var(--second-color);
  box-shadow: 0 0 8px #a5a5a5;
  z-index: 2;
  border-radius: 25px;
  color: #bbb;
}

.pri {
  background-image: linear-gradient(180deg, #6d40f5, #4070f4);
  background-image: linear-gradient(180deg, var(--third-color), var(--first-color));
}

.pri:hover {
  background-image: linear-gradient(#40cdf5, #4070f4);
  background-image: linear-gradient(var(--forth-color), var(--first-color));
  color: #ccc;
}

.sec {
  border: 1px solid #4070f4;
  border: 1px solid var(--first-color);
  color: #4070f4;
  color: var(--first-color);
  background-color: #fff;
}

.sec:hover {
  color: #6d40f5;
  color: var(--third-color);
}

/* ===== ABOUT =====*/
.about__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
  text-align: center;
}

.about__subtitle {
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.about__img {
  -ms-grid-column-align: center;
      justify-self: center;
}

.about__img img {
  width: 200px;
  border-radius: 0.5rem;
}

/* ===== SKILLS =====*/
.skills__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
  text-align: center;
}

.skills__subtitle {
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.skills__text {
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.skills__data {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  font-weight: 600;
  font-weight: var(--font-semi);
  padding: 0.5rem 1rem;
  background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
}

.skills__icon {
  font-size: 2rem;
  margin-right: 1rem;
  margin-right: var(--mb-2);
  color: #4070f4;
  color: var(--first-color);
}

.skills__names {
  display: flex;
  align-items: center;
}

.skills__bar {
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: #4070f4;
  background-color: var(--first-color);
  height: 0.25rem;
  border-radius: 0.5rem;
  z-index: -10;
  z-index: var(--z-back);
}

.skills__html {
  width: 95%;
}

.skills__css {
  width: 85%;
}

.skills__js {
  width: 65%;
}

.skills__ux {
  width: 85%;
}

.skills__img {
  border-radius: 0.5rem;
}

/* ===== WORK =====*/
.work__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.work__img {
  display: block;
  position: relative;
  overflow: hidden;
  border-radius: 0.6rem;
  margin: 0;
  padding: 0;
  padding-top: 50%;
  box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
}

.work__img img {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 100%;
  cursor: pointer;
}

.work__img img.project-thumb {
  transform: scale(1);
  transition: 0.25s ease-in-out;
}

.work .work__img .opON {
  display: block;
}

.work .overlay,
.work .figcaption {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: 0.45s ease-in-out;
}

.work .overlay {
  display: none;
}

.work .overlay::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(10, 10, 10, 0.7);
}

.work .figcaption {
  display: flex;
  flex-direction: column;
  padding: 0rem 1rem;
  color: #fff;
  z-index: 1;
  margin: 0;
  border: 0;
  font: inherit;
  justify-content: space-around;
}

.work .title {
  color: #fff;
  margin: 0;
  font-weight: 500;
  font-size: 1.3rem;
  text-align: center;
}

.work .btns {
  display: inherit;
}

.work .button {
  display: inline-flex;
  align-items: center;
  box-shadow: none;
  height: 2.3rem;
  position: relative;
  margin: 0 auto;
  padding: 1rem;
  border: 2px solid #4070f4;
  border: 2px solid var(--first-color);
  border-radius: 25px;
  background-color: transparent;
  font-weight: 400;
}

.work .button:hover {
  background-image: linear-gradient(#6d40f5, #4070f4);
  background-image: linear-gradient(var(--third-color), var(--first-color));
}

.work ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.work ul li {
  display: inline-block;
  padding: 0 0.5em;
  line-height: 25px;
  background-color: #313131;
  color: #999;
  border-width: 1px 1px 2px 1px;
  border-style: solid;
  border-color: #222;
  border-radius: 3px;
  text-transform: uppercase;
  font-family: MontSerrat, Tahoma, Arial, sans-serif;
  font-weight: 700;
  font-size: 0.75em;
  box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.25);
  margin: 2px;
  position: relative;
  text-shadow: -1px -1px 0 #3f3f3f, -1px 0 0 #3f3f3f, -1px 1px 0 #3f3f3f, 0 -1px 0 #3f3f3f, 0 0 0 #3f3f3f, 0 1px 0 #3f3f3f, 1px -1px 0 #3f3f3f, 1px 0 0 #3f3f3f, 1px 1px 0 #3f3f3f;
}

/* ===== CONTACT =====*/
.contact p {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  margin-bottom: var(--mb-1);
}

.contact .contact__connect {
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
  color: #6d40f5;
  color: var(--third-color);
}

.contact__container {
  justify-items: center;
}

.contact__social {
  display: flex;
  flex-direction: column;
}

.contact__social-icon {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
  font-size: 1.1rem;
  color: #4070f4;
  color: var(--first-color);
  padding: 0.4rem 0.8rem;
  border: 1px solid #4070f4;
  border: 1px solid var(--first-color);
}

.contact__social-icon i {
  margin-right: 1.5rem;
  margin-right: var(--mb-3);
  font-size: 1.5rem;
}

/* ===== MEDIA QUERIES=====*/
@media screen and (min-width: 768px) {
  body {
    margin: 0;
    height: 100vh;
    background: url(/static/media/Vector.767a7ff6.png) 100% 100%/20% no-repeat, url("/static/media/Vector(1).68d0b99d.png") 90% 7%/40% no-repeat, url("/static/media/Vector(2).9a25e56a.png") 0 100%/50% no-repeat;
  }
  .section {
    padding-top: 4rem;
    padding-bottom: 3rem;
    min-height: 100vh;
  }
  .section-title {
    margin-bottom: 3rem;
    margin-bottom: var(--mb-6);
  }
  .section-title::after {
    width: 120px;
    top: 4rem;
  }
  .nav {
    justify-content: center;
    height: calc(3rem + 1rem);
    height: calc(var(--header-height) + 1rem);
  }
  .nav__list {
    display: flex;
    padding-top: 0;
  }
  .nav__item {
    margin-left: 3rem;
    margin-left: var(--mb-6);
    margin-bottom: 0;
  }
  .nav__toggle, .nav__logo {
    display: none;
  }
  .nav__link {
    color: #0e2431;
    color: var(--second-color);
  }
  .home {
    height: 100vh;
  }
  .home__title-top {
    font-size: 2.5rem;
  }
  .home__title-mid {
    font-size: 3.5rem;
  }
  .home__title-bot {
    font-size: 1.6rem;
  }
  .home .home-buttons {
    justify-content: flex-start;
  }
  .home .home-buttons a:nth-child(1) {
    margin-right: 2rem;
    margin-right: var(--mb-4);
  }
  .about__container {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    align-items: center;
    text-align: left;
    text-align: initial;
  }
  .about__img img {
    width: 270px;
  }
  .skills__container {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    align-items: center;
    text-align: left;
    text-align: initial;
  }
  .work__container {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    -ms-grid-rows: (1fr)[2];
        grid-template-rows: repeat(2, 1fr);
    -webkit-column-gap: 2rem;
            grid-column-gap: 2rem;
            column-gap: 2rem;
  }
  .contact p {
    font-size: 1.5rem;
  }
  .contact__social-icon {
    font-size: 1.3rem;
    padding: 0.5rem 1rem;
  }
  .contact__social-icon i {
    font-size: 1.8rem;
  }
}

@media screen and (min-width: 1024px) {
  body {
    background: url(/static/media/Vector.767a7ff6.png) 100% 100%/20% no-repeat fixed, url("/static/media/Vector(1).68d0b99d.png") 90% 7%/30% no-repeat fixed, url("/static/media/Vector(2).9a25e56a.png") 0 100%/40% no-repeat fixed;
  }
  .bd-grid {
    margin-left: auto;
    margin-right: auto;
  }
  .home__title-top {
    font-size: 3.5rem;
  }
  .home__title-mid {
    font-size: 5rem;
  }
  .home__title-bot {
    font-size: 2rem;
  }
  .work__container {
    -ms-grid-columns: (1fr)[3];
        grid-template-columns: repeat(3, 1fr);
    -ms-grid-rows: (1fr)[2];
        grid-template-rows: repeat(2, 1fr);
    -webkit-column-gap: 2rem;
            grid-column-gap: 2rem;
            column-gap: 2rem;
  }
  .work .overlay {
    display: block;
  }
  .work .overlay,
  .work .figcaption {
    opacity: 0;
  }
  .work__img:hover .overlay,
  .work__img:hover .figcaption {
    opacity: 1;
  }
}

html {
  margin-left: calc(100vw - 100%);
  margin-right: 0;
}

.goToWork {
  background-color: #dad;
  padding: 3px 5px;
  margin-left: 10px;
  border-radius: 10px;
  color: white;
  font-weight: 600;
}

.goToWork:hover {
  color: #0e2431;
  color: var(--second-color);
  background-color: #fbf;
}

.slide-forward-enter,
.slide-forward-exit {
  transition: transform 1000ms ease-out;
}

.slide-forward-enter {
  transform: translateX(100%);
}

.slide-forward-enter.slide-forward-enter-active {
  transform: translateX(0%);
}

.slide-forward-exit {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transform: translateX(0%);
}

.slide-forward-exit-active {
  transform: translateX(-100%);
}

.slide-backward-enter,
.slide-backward-exit {
  transition: transform 1000ms ease-out;
}

.slide-backward-enter {
  transform: translateX(-100%);
}

.slide-backward-enter.slide-backward-enter-active {
  transform: translateX(0%);
}

.slide-backward-exit {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transform: translateX(0%);
}

.slide-backward-exit-active {
  transform: translateX(100%);
}

.slide-up-enter,
.slide-up-exit {
  transition: transform 1000ms ease-out;
}

.slide-up-enter {
  transform: translateY(100%);
}

.slide-up-enter.slide-up-enter-active {
  transform: translateY(0%);
}

.slide-up-exit {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transform: translateY(0%);
}

.slide-up-exit-active {
  transform: translateY(-100%);
}

.slide-down-enter,
.slide-down-exit {
  transition: transform 1000ms ease-out;
}

.slide-down-enter {
  transform: translateY(-100%);
}

.slide-down-enter.slide-down-enter-active {
  transform: translateY(0%);
}

.slide-down-exit {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transform: translateY(0%);
}

.slide-down-exit-active {
  transform: translateY(100%);
}

.page-control {
  z-index: var(--z-fixed);
  position: fixed;
}

.prev,
.next {
  display: none;
  position: fixed;
  top: 40%;
  width: 3rem;
  height: 20%;
  line-height: 2rem;
  cursor: pointer;
  transition: transform 0.7s cubic-bezier(0.7, 0, 0.25, 1) 0.25s, opacity 0.2s ease-out 0.75s;
}

.prev:hover,
.next:hover {
  color: var(--third-color);
}

.prev:hover span,
.next:hover span {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.prev-txt,
.next-txt {
  display: none;
  position: absolute;
  white-space: nowrap;
  top: calc(50% - 1.5rem);
  line-height: 3rem;
  padding: 0 0.6rem;
  opacity: 0;
  text-transform: lowercase;
  font-size: 1.2rem;
  transition: opacity 0.1s ease 0.125s, transform 0.3s cubic-bezier(0, 0, 0, 1) 0.125s;
}

.prev i,
.next i {
  display: block;
  position: absolute;
  top: calc(50% - 1.5rem);
  transition: transform 0.2s ease-out 0.2s;
  font-size: 3rem;
}

.prev {
  left: 0;
}

.prev-txt {
  left: calc(50% + 0.7rem);
  transform: translate3d(-30px, 0, 0);
}

.prev i {
  left: calc(50% - 1.5rem);
}

.next {
  right: 0;
}

.next-txt {
  right: calc(50% + 0.7rem);
  transform: translate3d(30px, 0, 0);
}

.next i {
  right: calc(50% - 1.5rem);
}

@media screen and (min-width: 768px) {
  .prev,
  .next {
    display: block;
  }
}

@media screen and (min-width: 1024px) {
  .prev,
  .next {
    width: 6rem;
  }
  .prev-txt,
  .next-txt {
    display: inline-block;
  }
}

