.slide-forward-enter,
.slide-forward-exit {
  -webkit-transition: -webkit-transform 1000ms ease-out;
  transition: -webkit-transform 1000ms ease-out;
  transition: transform 1000ms ease-out;
  transition: transform 1000ms ease-out, -webkit-transform 1000ms ease-out;
}

.slide-forward-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.slide-forward-enter.slide-forward-enter-active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.slide-forward-exit {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.slide-forward-exit-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

