/*===== GOOGLE FONTS =====*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");
/*===== VARIABLES CSS =====*/
:root {
  --header-height: 3rem;
  --font-semi: 600;
}

/*===== Colores =====*/
:root {
  --first-color: #4070f4;
  --second-color: #0e2431;
  --third-color: #6d40f5;
  --forth-color: #40cdf5;
}

/*===== Fuente y tipografia =====*/
:root {
  --body-font: "Poppins", sans-serif;
  --big-font-size: 2rem;
  --h2-font-size: 2rem;
  --normal-font-size: 0.938rem;
}

@media screen and (min-width: 768px) {
  :root {
    --big-font-size: 3.5rem;
    --h2-font-size: 3rem;
    --normal-font-size: 1rem;
  }
}

/*===== Margenes =====*/
:root {
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
}

/*===== z index =====*/
:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}

/*===== BASE =====*/
*,
::before,
::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: var(--header-height) 0 0 0;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  color: var(--second-color);
  height: calc(100vh - var(--header-height));
  background: url("./assets/img/Vector.png") 100% 100%/20% no-repeat, url("./assets/img/Vector(1).png") 100% 0%/60% no-repeat, url("./assets/img/Vector(2).png") -10% 100%/60% no-repeat;
}

h1,
h2,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

/*===== CLASS CSS ===== */
.section-title {
  position: relative;
  font-size: var(--h2-font-size);
  color: var(--first-color);
  margin-top: var(--mb-2);
  margin-bottom: var(--mb-4);
  text-align: center;
}

.section-title::after {
  position: absolute;
  content: "";
  width: 5rem;
  height: 0.18rem;
  left: 0;
  right: 0;
  margin: auto;
  top: 2.5rem;
  background-color: var(--first-color);
}

.section {
  padding-top: 3rem;
  padding-bottom: 2rem;
  min-height: calc(100vh - var(--header-height));
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
}

/*===== LAYOUT =====*/
.bd-grid {
  max-width: 1024px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 100%;
      grid-template-columns: 100%;
  grid-column-gap: 2rem;
  width: calc(100% - 2rem);
  margin-left: var(--mb-2);
  margin-right: var(--mb-2);
}

.l-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
}

/*===== NAV =====*/
.nav {
  height: var(--header-height);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-weight: var(--font-semi);
}

@media screen and (max-width: 767px) {
  .nav__menu {
    position: fixed;
    top: var(--header-height);
    right: -100%;
    width: 80%;
    height: 100%;
    padding: 2rem;
    background-color: var(--second-color);
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
}

.nav__item {
  margin-bottom: var(--mb-4);
}

.nav__link {
  position: relative;
  color: #fff;
}

.nav__link:hover {
  position: relative;
}

.nav__link:hover::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: var(--first-color);
}

.nav__logo {
  font-size: 1.3rem;
  font-weight: 500;
  background-color: #a2a5e1;
  -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
          box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
  color: #fff;
  padding: 0.1rem 0.5rem;
  border-radius: 1.3rem;
}

.nav__toggle {
  color: var(--second-color);
  font-size: 1.5rem;
  cursor: pointer;
}

/*Active menu*/
.active::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: var(--first-color);
}

/*=== Show menu ===*/
.show {
  right: 0;
}

/*===== HOME =====*/
.home {
  height: calc(100vh - var(--header-height));
  row-gap: 1rem;
}

.home__data {
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
}

.home__title {
  margin-bottom: var(--mb-5);
  text-align: left;
}

.home__title-top {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #36364b;
}

.home__title-mid {
  font-size: 2.5rem;
  letter-spacing: 5px;
  font-weight: 500;
  text-align: left;
  font-family: "Montserrat", sans-serif;
  color: var(--second-color);
}

.home__title-bot {
  font-size: 1.3rem;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  letter-spacing: 2px;
  color: #444449;
}

.home .home-buttons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin-top: var(--mb-6);
}

/*BUTTONS*/
.button {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 8.2rem;
  height: 3rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-family: roboto;
  letter-spacing: 1px;
  color: var(--second-color);
  -webkit-box-shadow: 0 0 8px #a5a5a5;
          box-shadow: 0 0 8px #a5a5a5;
  z-index: 2;
  border-radius: 25px;
  color: #bbb;
}

.pri {
  background-image: -webkit-gradient(linear, left top, left bottom, from(var(--third-color)), to(var(--first-color)));
  background-image: linear-gradient(180deg, var(--third-color), var(--first-color));
}

.pri:hover {
  background-image: -webkit-gradient(linear, left top, left bottom, from(var(--forth-color)), to(var(--first-color)));
  background-image: linear-gradient(var(--forth-color), var(--first-color));
  color: #ccc;
}

.sec {
  border: 1px solid var(--first-color);
  color: var(--first-color);
  background-color: #fff;
}

.sec:hover {
  color: var(--third-color);
}

/* ===== ABOUT =====*/
.about__container {
  row-gap: 2rem;
  text-align: center;
}

.about__subtitle {
  margin-bottom: var(--mb-2);
}

.about__img {
  -ms-grid-column-align: center;
      justify-self: center;
}

.about__img img {
  width: 200px;
  border-radius: 0.5rem;
}

/* ===== SKILLS =====*/
.skills__container {
  row-gap: 2rem;
  text-align: center;
}

.skills__subtitle {
  margin-bottom: var(--mb-2);
}

.skills__text {
  margin-bottom: var(--mb-4);
}

.skills__data {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  font-weight: var(--font-semi);
  padding: 0.5rem 1rem;
  background-color: #fff;
  border-radius: 0.5rem;
  -webkit-box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
          box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
}

.skills__icon {
  font-size: 2rem;
  margin-right: var(--mb-2);
  color: var(--first-color);
}

.skills__names {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.skills__bar {
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: var(--first-color);
  height: 0.25rem;
  border-radius: 0.5rem;
  z-index: var(--z-back);
}

.skills__html {
  width: 95%;
}

.skills__css {
  width: 85%;
}

.skills__js {
  width: 65%;
}

.skills__ux {
  width: 85%;
}

.skills__img {
  border-radius: 0.5rem;
}

/* ===== WORK =====*/
.work__container {
  row-gap: 2rem;
}

.work__img {
  display: block;
  position: relative;
  overflow: hidden;
  border-radius: 0.6rem;
  margin: 0;
  padding: 0;
  padding-top: 50%;
  -webkit-box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
          box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
}

.work__img img {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 100%;
  cursor: pointer;
}

.work__img img.project-thumb {
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

.work .work__img .opON {
  display: block;
}

.work .overlay,
.work .figcaption {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  -webkit-transition: 0.45s ease-in-out;
  transition: 0.45s ease-in-out;
}

.work .overlay {
  display: none;
}

.work .overlay::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(10, 10, 10, 0.7);
}

.work .figcaption {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 0rem 1rem;
  color: #fff;
  z-index: 1;
  margin: 0;
  border: 0;
  font: inherit;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.work .title {
  color: #fff;
  margin: 0;
  font-weight: 500;
  font-size: 1.3rem;
  text-align: center;
}

.work .btns {
  display: inherit;
}

.work .button {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-shadow: none;
          box-shadow: none;
  height: 2.3rem;
  position: relative;
  margin: 0 auto;
  padding: 1rem;
  border: 2px solid var(--first-color);
  border-radius: 25px;
  background-color: transparent;
  font-weight: 400;
}

.work .button:hover {
  background-image: -webkit-gradient(linear, left top, left bottom, from(var(--third-color)), to(var(--first-color)));
  background-image: linear-gradient(var(--third-color), var(--first-color));
}

.work ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.work ul li {
  display: inline-block;
  padding: 0 0.5em;
  line-height: 25px;
  background-color: #313131;
  color: #999;
  border-width: 1px 1px 2px 1px;
  border-style: solid;
  border-color: #222;
  border-radius: 3px;
  text-transform: uppercase;
  font-family: MontSerrat, Tahoma, Arial, sans-serif;
  font-weight: 700;
  font-size: 0.75em;
  -webkit-box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.25);
          box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.25);
  margin: 2px;
  position: relative;
  text-shadow: -1px -1px 0 #3f3f3f, -1px 0 0 #3f3f3f, -1px 1px 0 #3f3f3f, 0 -1px 0 #3f3f3f, 0 0 0 #3f3f3f, 0 1px 0 #3f3f3f, 1px -1px 0 #3f3f3f, 1px 0 0 #3f3f3f, 1px 1px 0 #3f3f3f;
}

/* ===== CONTACT =====*/
.contact p {
  font-size: 1.2rem;
  margin-bottom: var(--mb-1);
}

.contact .contact__connect {
  margin-bottom: var(--mb-2);
  color: var(--third-color);
}

.contact__container {
  justify-items: center;
}

.contact__social {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.contact__social-icon {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: var(--mb-2);
  font-size: 1.1rem;
  color: var(--first-color);
  padding: 0.4rem 0.8rem;
  border: 1px solid var(--first-color);
}

.contact__social-icon i {
  margin-right: var(--mb-3);
  font-size: 1.5rem;
}

/* ===== MEDIA QUERIES=====*/
@media screen and (min-width: 768px) {
  body {
    margin: 0;
    height: 100vh;
    background: url("./assets/img/Vector.png") 100% 100%/20% no-repeat, url("./assets/img/Vector(1).png") 90% 7%/40% no-repeat, url("./assets/img/Vector(2).png") 0 100%/50% no-repeat;
  }
  .section {
    padding-top: 4rem;
    padding-bottom: 3rem;
    min-height: 100vh;
  }
  .section-title {
    margin-bottom: var(--mb-6);
  }
  .section-title::after {
    width: 120px;
    top: 4rem;
  }
  .nav {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    height: calc(var(--header-height) + 1rem);
  }
  .nav__list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-top: 0;
  }
  .nav__item {
    margin-left: var(--mb-6);
    margin-bottom: 0;
  }
  .nav__toggle, .nav__logo {
    display: none;
  }
  .nav__link {
    color: var(--second-color);
  }
  .home {
    height: 100vh;
  }
  .home__title-top {
    font-size: 2.5rem;
  }
  .home__title-mid {
    font-size: 3.5rem;
  }
  .home__title-bot {
    font-size: 1.6rem;
  }
  .home .home-buttons {
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
  }
  .home .home-buttons a:nth-child(1) {
    margin-right: var(--mb-4);
  }
  .about__container {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: initial;
  }
  .about__img img {
    width: 270px;
  }
  .skills__container {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: initial;
  }
  .work__container {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    -ms-grid-rows: (1fr)[2];
        grid-template-rows: repeat(2, 1fr);
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
  }
  .contact p {
    font-size: 1.5rem;
  }
  .contact__social-icon {
    font-size: 1.3rem;
    padding: 0.5rem 1rem;
  }
  .contact__social-icon i {
    font-size: 1.8rem;
  }
}

@media screen and (min-width: 1024px) {
  body {
    background: url("./assets/img/Vector.png") 100% 100%/20% no-repeat fixed, url("./assets/img/Vector(1).png") 90% 7%/30% no-repeat fixed, url("./assets/img/Vector(2).png") 0 100%/40% no-repeat fixed;
  }
  .bd-grid {
    margin-left: auto;
    margin-right: auto;
  }
  .home__title-top {
    font-size: 3.5rem;
  }
  .home__title-mid {
    font-size: 5rem;
  }
  .home__title-bot {
    font-size: 2rem;
  }
  .work__container {
    -ms-grid-columns: (1fr)[3];
        grid-template-columns: repeat(3, 1fr);
    -ms-grid-rows: (1fr)[2];
        grid-template-rows: repeat(2, 1fr);
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
  }
  .work .overlay {
    display: block;
  }
  .work .overlay,
  .work .figcaption {
    opacity: 0;
  }
  .work__img:hover .overlay,
  .work__img:hover .figcaption {
    opacity: 1;
  }
}

html {
  margin-left: calc(100vw - 100%);
  margin-right: 0;
}

.goToWork {
  background-color: #dad;
  padding: 3px 5px;
  margin-left: 10px;
  border-radius: 10px;
  color: white;
  font-weight: 600;
}

.goToWork:hover {
  color: var(--second-color);
  background-color: #fbf;
}
