.page-control {
  z-index: var(--z-fixed);
  position: fixed;
}

.prev,
.next {
  display: none;
  position: fixed;
  top: 40%;
  width: 3rem;
  height: 20%;
  line-height: 2rem;
  cursor: pointer;
  -webkit-transition: opacity 0.2s ease-out 0.75s, -webkit-transform 0.7s cubic-bezier(0.7, 0, 0.25, 1) 0.25s;
  transition: opacity 0.2s ease-out 0.75s, -webkit-transform 0.7s cubic-bezier(0.7, 0, 0.25, 1) 0.25s;
  transition: transform 0.7s cubic-bezier(0.7, 0, 0.25, 1) 0.25s, opacity 0.2s ease-out 0.75s;
  transition: transform 0.7s cubic-bezier(0.7, 0, 0.25, 1) 0.25s, opacity 0.2s ease-out 0.75s, -webkit-transform 0.7s cubic-bezier(0.7, 0, 0.25, 1) 0.25s;
}

.prev:hover,
.next:hover {
  color: var(--third-color);
}

.prev:hover span,
.next:hover span {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.prev-txt,
.next-txt {
  display: none;
  position: absolute;
  white-space: nowrap;
  top: calc(50% - 1.5rem);
  line-height: 3rem;
  padding: 0 0.6rem;
  opacity: 0;
  text-transform: lowercase;
  font-size: 1.2rem;
  -webkit-transition: opacity 0.1s ease 0.125s, -webkit-transform 0.3s cubic-bezier(0, 0, 0, 1) 0.125s;
  transition: opacity 0.1s ease 0.125s, -webkit-transform 0.3s cubic-bezier(0, 0, 0, 1) 0.125s;
  transition: opacity 0.1s ease 0.125s, transform 0.3s cubic-bezier(0, 0, 0, 1) 0.125s;
  transition: opacity 0.1s ease 0.125s, transform 0.3s cubic-bezier(0, 0, 0, 1) 0.125s, -webkit-transform 0.3s cubic-bezier(0, 0, 0, 1) 0.125s;
}

.prev i,
.next i {
  display: block;
  position: absolute;
  top: calc(50% - 1.5rem);
  -webkit-transition: -webkit-transform 0.2s ease-out 0.2s;
  transition: -webkit-transform 0.2s ease-out 0.2s;
  transition: transform 0.2s ease-out 0.2s;
  transition: transform 0.2s ease-out 0.2s, -webkit-transform 0.2s ease-out 0.2s;
  font-size: 3rem;
}

.prev {
  left: 0;
}

.prev-txt {
  left: calc(50% + 0.7rem);
  -webkit-transform: translate3d(-30px, 0, 0);
          transform: translate3d(-30px, 0, 0);
}

.prev i {
  left: calc(50% - 1.5rem);
}

.next {
  right: 0;
}

.next-txt {
  right: calc(50% + 0.7rem);
  -webkit-transform: translate3d(30px, 0, 0);
          transform: translate3d(30px, 0, 0);
}

.next i {
  right: calc(50% - 1.5rem);
}

@media screen and (min-width: 768px) {
  .prev,
  .next {
    display: block;
  }
}

@media screen and (min-width: 1024px) {
  .prev,
  .next {
    width: 6rem;
  }
  .prev-txt,
  .next-txt {
    display: inline-block;
  }
}
